.articles {
    @include is-sub-grid-container('2 / 3', null, 'repeat(3, 1fr)', '2');
    gap: 3vw;
    grid-auto-flow: dense;
    padding: 0 0 3vw;
    width: 100%;

    .mini-article {
        &:first-child {
            grid-row: span 2;
    
            @include mq-phone {
                grid-row: span 1;
            }
        }

        &:nth-child(5) {
            grid-row: span 2;
    
            @include mq-phone {
                grid-row: span 1;
            }
        }
    }

    @include mq-phone {
        grid-template-columns: 1fr;
    }
    
    @include mq-tablet {
        grid-template-columns: 1fr 1fr;
    }
}

.mini-article {
    @include is-grid-item(span 1);
    background: get-color(prim2);
    padding: 1.2vw;
    text-align: justify;
    text-justify: newspaper;


    figure {

        img {
            height: 20vh;
            max-height: 20vmax;
            min-height: 20vmin;
            object-fit: cover;
        }

        figcaption {
            color: get-color(prim1);
        }
    }

    h2 {
        font-weight: 400;
    }

    p {
        color: get-color(tri2);
        // margin: 1rem 0;
        margin-bottom: 1rem;
        padding: 0;
    }
}


.row {
    &--bg-tech {
        @extend %row;
        background-color: tint(get-color(tri1), 10%);
        background-image: url('../img/circuit2.svg');
        background-position: 0% / 50%;
        background-repeat: no-repeat;
        background-size: cover;
    }

    &__headline {
        @include is-grid-item('2 / 3', '1 / 2');
        font-size: 3rem;
        font-weight: 300;
        text-align: center;
    }
}
