////
/// This is a poster comment.
/// It will apply annotations to all items from file.
/// @group Program class
////
//

/// Class for the ubuntu program
.program {
    // @include is-grid-item('2 / 3');
    // @include is-sub-grid-container('2 / 3', null, '1fr 1fr');
    // @include is-sub-grid-container('2 / 3', null, 'minmax(3vw, auto) 1fr 1fr minmax(3vw, auto)');
    @include is-sub-grid-container('2 / 3', null, '0px 1fr 1fr 0px');
    // grid-column: 2 / 3;
    gap: 3vw;
    padding-bottom: 6vw;
    grid-auto-flow: dense;

    .mini-article {
        figure {

            img {
                height: 25vh;
                max-height: 25vmax;
                min-height: 25vmin;
                object-fit: cover;
            }
        }
    }

    /// Mixin to give common article properties
    @mixin article-props {
        & {
            $color: shade(get-color(prim2), 10%);
            color: $color;
            // font-weight: 400;

            figure {
                figcaption {
                    color: $color;
                }
            }

            a {
                color: $color;

                &:hover {
                    text-decoration-color: $color;
                }
            }

            &__headline {
                padding: 0;
                margin-top: 3vw;
                font-size: 3rem;
                font-weight: 400;
                color: tint(get-color(prim3), 85%);
                text-align: center;
                // margin: 3vw 0 0 0;

                @include mq-phone {
                    display: none;
                }

                @include mq-tablet {
                    display: none;
                }
            }

            @include mq-phone {
                grid-column: 2 / 4;
            }

            @include mq-tablet {
                grid-column: 2 / 4;
                // grid-row: -1;
            }
        }
    }

    /// iot element of the program class.
    &__iot {
        background: tint(get-color(prim1), 70%);
        grid-column: 2 / span 1;

        @include article-props;

        /// headline element of the iot class
        &__headline {
            grid-column: 2 / span 1;
            grid-row: 1 / 2;
            // color: get-color(prim1)
            // background: linear-gradient(90deg, rgba(255,255,255,0) 0%, tint(get-color(prim1), 80%) 25%, tint(get-color(prim1), 80%) 75%, rgba(255,255,255,0) 100%);
            // background: linear-gradient(90deg, rgba(255,255,255,0) 0%, tint(get-color(prim1), 80%) 50%, rgba(255,255,255,0) 100%);
        }
    }

    /// desktop element of the program class
    &__desktop {
        background: tint(get-color(sec1), 70%);
        grid-column: 3 / span 1;

        @include article-props;

        /// headline element of the desktop class
        &__headline {
            grid-column: 3 / span 1;
            grid-row: 1 / 2;
            // color: get-color(sec1);
            // background: linear-gradient(90deg, rgba(255,255,255,0) 0%, tint(get-color(sec1), 80%) 25%, tint(get-color(sec1), 80%) 75%, rgba(255,255,255,0) 100%);
            // background: linear-gradient(90deg, rgba(255,255,255,0) 0%, tint(get-color(sec1), 80%) 50%, rgba(255,255,255,0) 100%);
        }
    }
}


